import React, { PureComponent } from 'react';
import { withStyles, Paper, Box } from "@material-ui/core";
import {
    DESK_WIDTH,
    DESK_HEIGHT,
} from './constants';

const DEFAULT_IMAGE = 'data:image/png;base64,';

const styles = () => ({
    screen: {
       position: 'relative',
    },
    image: {
        display: 'block',
        width: DESK_WIDTH,
        height: DESK_HEIGHT,
        // width: 300,
        // height: getHeightFromWidth(300, DESK_WIDTH, DESK_HEIGHT),
    },
    note: {
        width: DESK_WIDTH,
        height: DESK_HEIGHT,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        flexWrap: 'wrap',
    }
});

class Screen extends PureComponent {

    render() {
        const { classes, image } = this.props;
        return (
            <Paper
                elevation={5}
                square={true}
                className={classes.screen}
            >
                {
                    image && image.length ? <img src={image || DEFAULT_IMAGE} className={classes.image} /> :
                        (
                            <Box
                                className={classes.note}
                            >
                                The game is going to start
                            </Box>
                        )
                }
            </Paper>
        )
    }
}

export default withStyles(styles)(Screen);